import { Board, Score, Piece, Player, Controller, DefaultState } from "../lib/index.js";
import { WhiteTurnState, BlackTurnState } from "./states/index.js";
import { PieceFactory } from "./PieceFactory.js";
import { Menu } from "./menu.js";
import { icons, whiteTypes, blackTypes } from "./consts.js";


export class ChessBoard {
    constructor() {
        const size = 8;
        this.board = this._createBoard(size, size);
        this.white = this._createPlayer("white");
        this.black = this._createPlayer("black");
        this.state = this._createController();
        this.menu = new Menu(this);
        this.pieces = new PieceFactory({
            white: this.white,
            black: this.black,
        });

        this._setupPlaces();
        this._resetPieces();

        this.dom = this._createDOM();
        this.state.to('white-turn');
        this.record = [];
    }

    _createPlayer(name) {
        return {
            player: new Player(name),
            score: new Score(name),
        };
    }

    _createDOM() {
        const leftSection = $('<div>')
              .attr('id', 'left')
              .append(this.menu.dom);

        const centerSection = $('<div>')
              .attr('id', 'center')
              .append(this.white.score.dom)
              .append(this.board.dom)
              .append(this.black.score.dom);

        const rightSection = $('<div>').attr('id', 'right');

        return  $('<div>')
            .attr('id', 'game')
            .append(leftSection)
            .append(centerSection)
            .append(rightSection);
    }

    _createBoard(width, height) {
        return new Board(width, height);
    }

    _createController() {
        return new Controller({
            "white-turn": new WhiteTurnState(this),
            "black-turn": new BlackTurnState(this),
        });
    }

    _setupPlace(pos, place) {
        place.dom.droppable({
            accept: this.state.delegate('shouldAccept', place),
            drop: this.state.delegate('pieceDropped', place),
            classes: {
                "ui-droppable-active": "ui-droppable-active",
            },
        });
    }

    _setupPlaces() {
        for (let [pos, place] of Object.entries(this.board.places)) {
            this._setupPlace(pos, place);
        }
    }

    _createPiece(color, name) {
        const piece = this.pieces.make(color, name);
        piece.dom.draggable({
            revert: "invalid",
            revertDuration: 100,
            start: () => piece.calculateMoves(this.board),
        });
        return piece;
    }

    _createWhite(size) {
        for (let i = 0; i < size; i++) {
            const piece = this._createPiece("white", "pawn");
            this.white.player.assign(piece);
            this.board.add([1, i], piece);
        }
        for (let i = 0; i < size; i++) {
            const name = whiteTypes[i];
            const piece = this._createPiece("white", name);
            this.white.player.assign(piece);
            this.board.add([0, i], piece);
        }
    }

    _createBlack(size) {
        const black = new Player("black");

        for (let i = 0; i < size; i++) {
            const piece = this._createPiece("black", "pawn");
            this.black.player.assign(piece);
            this.board.add([size - 2, i], piece);
        }
        for (let i = 0; i < size; i++) {
            const name = blackTypes[i];
            const piece = this._createPiece("black", name);
            this.black.player.assign(piece);
            this.board.add([size - 1, i], piece);
        }
    }

    _resetPieces() {
        this._createWhite(8);
        this._createBlack(8);
    }

    attach(selector) {
        this.dom.appendTo($(selector));
    }

    piecePosition(piece) {
        if (piece.place == this.white.score) {
            return "white"
        }
        if (piece.place == this.black.score) {
            return "black"
        }
        return piece.place.pos;
    }

    dumpPiece(piece) {
        return {
            color: piece.player.name,
            name: piece.name,
            pos: this.piecePosition(piece),
        }
    }

    dump() {
        return {
            whiteCaptures: this.white.score.captures
                .map(this.dumpPiece, this),
            blackCaptures: this.black.score.captures
                .map(this.dumpPiece, this),
            board: Object.values(this.board.places)
                .filter(p => p.piece != undefined, this)
                .map(p => this.dumpPiece(p.piece), this),
        }
    }

    clear() {
        this.board.clear();
        this.white.score.clear();
        this.black.score.clear();
        this.white.player.pieces = [];
        this.black.player.pieces = [];
    }

    reset() {
        this.clear();
        this._resetPieces();
    }

    load(record) {
        for (let {color, name, pos} of record.whiteCaptures) {
            const {player, score} = this[color];
            const piece = this._createPiece(color, name);
            player.assign(piece);
            score.add(piece);
        }
        for (let {color, name, pos} of record.blackCaptures) {
            const {player, score} = this[color];
            const piece = this._createPiece(color, name);
            player.assign(piece);
            score.add(piece);
        }
        for (let {color, name, pos} of record.board) {
            const {player, score} = this[color];
            const piece = this._createPiece(color, name);
            player.assign(piece);
            this.board.add(pos, piece);
        }
    }
}
