import { clearStyle } from "./utils.js";

export class Place {
    constructor(pos, color) {
        this.pos = pos;
        this.color = color;
        this.dom = $('<div>')
            .attr('id', `${pos[0]}-${pos[1]}`)
            .addClass(`place ${color}`)
            .data({pos});

        this._piece = undefined;
    }

    get piece() {
        return this._piece;
    }

    set piece(piece) {
        piece.detach();
        piece.dom.data({place: this});
        this.dom.append(piece.dom);
        this._piece = piece;
    }

    detach() {
        this._piece = undefined;
    }
}
