import { clearStyle } from "./utils.js";
import { icons } from "./consts.js";

export class Piece {
    constructor(name, icon) {
        this.name = name;
        this.icon = icon;
        this.moves = [];
        this.dom = $('<div>')
            .addClass([name, 'piece'])
            .text(this.icon)
            .data({piece: this});
    }

    detach() {
        this.dom.detach();
        clearStyle(this.dom);
        if (this.place) {
            this.data.place.detach();
            this.data.place = undefined;
        }
    }

    destroy() {
        this.dom.remove();
        this.detach();
    }

    get data() {
        return this.dom.data();
    }

    get player() {
        return this.data.player;
    }

    get place() {
        return this.data.place;
    }
}
