import { clearStyle } from "./utils.js";

export class Score {
    constructor(label) {
        this.label = label;
        this.dom = this._createDOM();
        this.container = this.dom.find('.captures');
        this.captures = [];
    }

    _createDOM() {
        return $(`
<div>
  <span>${this.label}</span>
  <div class="captures"></div>
</div>`).addClass('score');
    }

    add(piece) {
        piece.dom.detach();
        piece.dom.data({place: this});
        this.captures.push(piece);
        this.container.append(piece.dom);
        clearStyle(piece.dom);
    }

    get() {
        return this.captures;
    }

    detach(piece) {
        if (this.captures.includes(piece)) {
            this.captures.remove(piece);
        }
    }

    clear() {
        for (let capture of this.captures) {
            capture.destroy();
        }
    }
}
