import { Raycaster } from "./raycaster.js";
import { Piece } from "../../lib/piece.js";
import { directions, pawnRanks, orthogonals } from "../consts.js";


export class Rook extends Piece {
    constructor(name, icon) {
        super(name, icon);
        this.raycaster = new Raycaster(orthogonals);
    }

    calculateMoves(board) {
        this.moves = this.raycaster.calculateMoves(board, this);
    }
}
