import { icons } from "./consts.js";
import { King, Queen, Knight, Bishop, Rook, Pawn } from "./pieces/index.js";

export class PieceFactory {
    constructor(players) {
        this.players = players;
    }

    make(playerName, pieceName) {
        const player = this.players[playerName];
        const cls = this.getClass(pieceName);
        const icon = icons[pieceName];
        return new cls(pieceName, icon);
    }

    getClass(name) {
        switch (name) {
        case "king":
            return King;
        case "queen":
            return Queen;
        case "knight":
            return Knight;
        case "bishop":
            return Bishop;
        case "rook":
            return Rook;
        case "pawn":
            return Pawn;
        }
    }
}
