

export class Menu {
    constructor(game) {
        this.game = game;
        this.dom = this._createDOM();
    }

    _createDOM() {
        const resetButton = $(`<div id="reset"><i>\u23EA</i></div>`)
              .on('click', () => this.game.reset());

        const left = $('<div id="menuLeft">')
              .append(resetButton);

        const right = $('<div id="menuRight">')
              .append("<button>Foo</button>")
              .append("<button>Bar</button>");

        return $('<div>')
            .attr('id', 'menu')
            .append(left)
            .append(right);
    }
}
