import { Piece } from "../../lib/piece.js";
import { Raycaster } from "./raycaster.js";
import { diagonals, orthogonals } from "../consts.js";


export class Queen extends Piece {
    constructor(...args) {
        super(...args);
        this.raycaster = new Raycaster([...diagonals, ...orthogonals]);
    }

    calculateMoves(board) {
        this.moves = this.raycaster.calculateMoves(board, this);
    }
}
