import { Score } from "../../lib/score.js";


export class Raycaster {
    constructor(directions) {
        this.directions = directions;
    }

    calculateDirection(board, direction, piece) {
        let offset = piece.place.pos;
        while (true) {
            offset = offset.vecSum(direction);
            const place = board.at(offset);

            // out of bounds
            if (place == undefined){
                break;
            }

            // own piece
            if (place.piece && place.piece.player == piece.player) {
                break;
            }

            this.moves.push(place);

            // enemy piece
            if (place.piece) {
                break;
            }
        }
    }

    calculateMoves(board, piece) {
        this.moves = [];

        if (piece.place instanceof Score) {
            return moves;
        }

        for (let direction of this.directions) {
            this.calculateDirection(board, direction, piece);
        }

        return this.moves;
    }
}
