import { Piece } from "../../lib/piece.js";
import { Offsetter } from "./offsetter.js";
import { orthogonals, diagonals } from "../consts.js";


export class King extends Piece {
    constructor(name, icon) {
        super(name, icon);
        this.offsetter = new Offsetter([...orthogonals, ...diagonals]);
    }

    calculateMoves(board) {
        this.moves = this.offsetter.calculateMoves(board, this);
    }
}
