import { Piece } from "../../lib/piece.js";
import { Score } from "../../lib/score.js";
import { directions, pawnRanks } from "../consts.js";


export class Pawn extends Piece {
    get direction() {
        return directions[this.player.name];
    }

    get pawnRank() {
        return pawnRanks[this.player.name];
    }

    get advanceOffset(){
        return [this.direction, 0];
    }

    get doubleAdvanceOffset() {
        return [this.direction * 2, 0];
    }

    get attackOffsets() {
        return [
            this.advanceOffset.vecSum([0, -1]),
            this.advanceOffset.vecSum([0, +1]),
        ];
    }

    getAdvancePlace(board) {
        return board.at(this.place.pos.vecSum(this.advanceOffset));
    }

    getDoubleAdvancePlace(board) {
        return board.at(this.place.pos.vecSum(this.doubleAdvanceOffset));
    }

    getAttackPlaces(board) {
        return this.attackOffsets.map(o => board.at(this.place.pos.vecSum(o)));
    }

    calculateMoves(board) {
        this.moves = [];
        if (this.place instanceof Score) {
            return;
        }
        this.calculateAdvance(board);
        this.calculateDoubleAdvance(board);
        this.calculateAttacks(board);
    }

    calculateAdvance(board) {
        const advancePlace = this.getAdvancePlace(board);
        if (advancePlace == undefined) {
            return;
        }
        if (advancePlace.piece == undefined) {
            this.moves.push(advancePlace);
        }
    }

    calculateDoubleAdvance(board) {
        if (this.place.pos[0] != this.pawnRank) {
            return;
        }

        const advancePlace = this.getAdvancePlace(board);
        const doubleAdvancePlace = this.getDoubleAdvancePlace(board);
        if (advancePlace == undefined || doubleAdvancePlace == undefined) {
            return;
        }
        if (advancePlace.piece == undefined &&
            doubleAdvancePlace.piece == undefined) {
            this.moves.push(doubleAdvancePlace);
        }
    }

    calculateAttacks(board) {
        const attackPlaces = this.getAttackPlaces(board);
        for (let attackPlace of attackPlaces) {
            if (attackPlace == undefined) {
                continue;
            }
            if (attackPlace.piece != undefined &&
                attackPlace.piece.player != this.player) {
                this.moves.push(attackPlace);
            }
        }
    }
}
