import { Score } from "../../lib/score.js";


export class Offsetter {
    constructor(offsets) {
        this.offsets = offsets;
    }

    calculateOffset(board, offset, piece) {
        let pos = piece.place.pos.vecSum(offset);
        const place = board.at(pos);

        // out of bounds
        if (place == undefined){
            return;
        }

        // own piece
        if (place.piece && place.piece.player == piece.player) {
            return;
        }

        this.moves.push(place);
    }

    calculateMoves(board, piece) {
        this.moves = [];

        if (piece.place instanceof Score) {
            return moves;
        }

        for (let offset of this.offsets) {
            this.calculateOffset(board, offset, piece);
        }

        return this.moves;
    }
}
