import { Place } from "./place.js";


export class Board {
    constructor(width, height, styles = ["surface-a", "surface-b"]) {
        this.width = width;
        this.height = height;
        this.styles = styles;
        this.dom = this._createDOM();
        this.places = this._generatePlaces();
    }

    // internal

    _style() {
        this.styles.rotate();
        return this.styles[0];
    }

    _createDOM() {
        return $('<div>').attr('id', 'board');
    }

    _generateRow() {
        const row = $('<div>').addClass('row');
        this.dom.append(row);
        this._style();
        return row;
    }

    _generatePlace(y, x, row) {
        const place = new Place([y, x], this._style());
        row.append(place.dom);
        return place;
    }

    _generatePlaces() {
        const places = {};
        for (let y = 0; y < this.height; y++) {
            const row = this._generateRow();
            for (let x = 0; x < this.width; x++) {
                const place = this._generatePlace(y, x, row);
                place.dom.append(`<p>${y}, ${x}</p>`);
                places[[y, x]] = place;
            }
        }
        return places;
    }

    // api

    at(loc) {
        if (loc == undefined) { return undefined; }
        const y = Number.parseInt(loc[0]);
        const x = Number.parseInt(loc[1]);
        return this.places[[y, x]];
    }

    add(pos, piece) {
        const place = this.at(pos);
        place.piece = piece;
    }

    clear() {
        for (let place of Object.values(this.places)) {
            if (place.piece) {
                place.piece.dom.remove();
                place.piece.detach();
            }
        }
    }
}
